@import url(https://fonts.googleapis.com/css?family=Raleway:400,500,700&display=swap);
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body{
position: absolute;
width: 1440px;
left: 0px;
top: 0px;
}

body {
  background: #1F1F1F;
  color: #FFF;
  font: 400 14px Raleway, sans-serif;
  -webkit-font-smoothing: antialiased;
  overflow-x: hidden;
}

.fonte-roxa{
  color: #9045F0;
}

.linha-horizontal{
  color: #fff;
  top: 99vh;
  width: 100vw;
  border: 1px solid #fff;
  position: absolute;
}
.container{
  max-width: 1100px;
  margin:auto;
  /* height: 250vh; */
  max-height: 300vh;
  display: flex;
  /* flex-direction: column; */
  justify-content: space-between;
}

.menu{
  width: 20%;
  height: 100vh;
  position: fixed;
  max-width: 250px;
}

.menu-lateral{
  padding-top: 5vh;
  height: 100%;
}

.paginas{
  margin-left: 30%;
  width: 100%;
  height: 100vh;
  max-width: 850px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.pagina-inicial{
  position: relative;
  width:100%;
  height:70vh;
}

.barra-lateral{
  position: fixed;
  width: 0px;
  height: 70vh;
  margin-top: 15vh;

  border: 2px solid #9045F0;
}

.conteudo{
  left:5%;
  top:40%;
  position:absolute;
  width:100%;
  height:100%;
  
  font-weight: normal;
  line-height: 1.2em;

  color: #FFFFFF;
}

.conteudo h2{
  margin-top: 0;
  text-align: center;
  /* display: none; */
  color: #9045F0;
  margin-bottom: 1em;
  font-size: 2em;
}

.txt-ola{
  font-size: 4.5em;
}

/* .txt-ola p,
.txt-sobre p{
  display: block;
} */




@media(max-width: 1475px){
  body{
    font-size: .8rem;
    line-height: 32px;
  }

  .icon{
    font-size: 16px !important;
  }
}

@media(min-width: 1301px){
  .dvProjetos{
    margin-bottom: 100px;
  }
}

@media(min-width: 900px) and (max-width: 1300px){
  body{
    font-size: .5rem;
  }

  .paginas{
    margin-left: 20%;
  }

  .dvProjetos{
    width: 50%;
    /* margin-left: 0%; */
  }

  .dvProjetos ul li img{
    width: 100%;
    height: 100px;
  }

  .dvProjetos ul .logo-safishare img{
    padding: 20px;
    padding-top: 0;
  }
}



@media(max-width: 899px){
  .dvProjetos{
    width: 25%;
    /* margin-left: 0%; */
  }

  .dvProjetos ul li img{
    width: 100%;
    height: 100px;
  }

  .dvProjetos ul .logo-safishare img{
    padding: 20px;
    padding-top: 0;
  }

  .dvProjetos ul {
    display: block;
  }

  .dvProjetos ul li {
    margin-top: 10px;
  }

  .dvProjetos{
    margin-bottom: 80px;
  }
}

@media(max-width: 700px){
  body{
    font-size: .4rem;
  }

  .barra-lateral{
    border: 1px solid #9045F0;
  }

}



.imgPerfil{
  display: flex;
  justify-content: center;
  margin: auto;
}

.menu-lateral .imgPerfil img{
  border-radius: 50%;
}

.titulo-perfil{
  font-size: 1em;
  line-height: 1.2em;
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1{
  color: white;
}

.titulo-perfil span{
  display: block;
  font-size: 2.5em;
  line-height: 1.2em;
}

.menu-lateral nav{
  position: absolute;
  bottom: 0;
  margin-bottom: 15vh;
  width: 100%;
}

.menu-lateral nav ul{
  list-style: none;
  font-size: 2em;
}

.menu-lateral nav ul li a{
  color: #FFF;
  text-decoration: none;
}

.menu-lateral nav ul li a:hover,
.menu-lateral nav ul li a .icon:hover{
  color: #9045F0 !important;
}

.menu-lateral nav ul li a .icon{
  position: absolute;
  right: 0;
  margin-right: 0;
}

@media(min-width: 1300px){
  
}

@media(min-width: 900px) and (max-width: 1300px){
  .menu-lateral nav ul li a .icon{
    margin-right: 20%;
  }
}

@media(max-width: 899px){
  .imgPerfil img{
    width: 96px;
    height: 96px;
  }

  /* .menu-lateral nav ul li a .icon{
    margin-right: 0;
  } */
}
.sobre-mim{
  position: relative;
  width:100%;
  height:50vh;
}

.txt-sobre{
  /* margin-top: 60vh; */
  top: 0;
  font-size: 2em;
  width: 100%;
}


@media(min-width: 1300px){
  .txt-sobre{
    width: 80%;
    font-size: 2em;
  }
}

@media(min-width: 900px) and (max-width: 1300px){
  .txt-sobre{
    width: 50%;
    font-size: 3em;
  }
}

@media(max-width: 899px){
  .txt-sobre{
    width: 30%;
    font-size: 2.5em;
  }
}

.fundo-galeria{
  width: 100%;
  height: 100vh;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
}

.conteudo-galeria{
  width: 50%;
  height: 50%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10%;
}
.projetos{
  position: relative;
  width:100%;
  height:70vh;
  max-width: 700px;
}

.dvProjetos{
  /* margin-top: 60vh; */
  top: 0;
  font-size: 2em;
  width: 100%;
}

.dvProjetos ul{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 24px;
  list-style: none;
  margin-top: 15%;
  width: 100%;
}

.dvProjetos ul li img{
  background-color: #fff;
  width: 100%;
  height: 200px;
  border-radius: 10px;
  object-fit: contain;
}

.dvProjetos ul li img{
  width: 100%;
  /* height: 300px; */
  border-radius: 10px;
}

.dvProjetos ul .logo-safishare img{
  padding: 50px;
  padding-top: 0;
}

.dvProjetos ul li {
  cursor: pointer;
}





